import { newSpecPage } from '@stencil/core/testing';
import { CTAOverlayUI } from '../../ui/presto-cta-overlay-ui';

describe('presto-cta-overlay-ui', () => {
  it('renders', async () => {
    const page = await newSpecPage({
      components: [CTAOverlayUI],
      html: `<presto-cta-overlay-ui></presto-cta-overlay-ui>`,
    });
    expect(page.root).toEqualHtml(`
    <presto-cta-overlay-ui>
       <mock:shadow-root>
          <div class="wrapper" style="font-size: 8px;">
            <div class="overlay">
              <div class="content">
                <h1 part="cta-headline"></h1>
              </div>
            </div>
          </div>
       </mock:shadow-root>
     </presto-cta-overlay-ui>
    `);
  });
});
